




































import Vue from 'vue'
import parser from 'mini-sql'
import JsonTree from 'vue-json-tree'

export default Vue.extend({
	components: {
		JsonTree,
	},
	data() {
		return {
			error: null as ParseError | null,
			parse: '',
			parsed: [] as object[],
		}
	},
	methods: {
		onParse() {
			this.error = null
			this.parsed = []
			try {
				this.parsed = parser.parse(this.parse)
			} catch (error) {
				this.error = error.hash
			}
		},
	},
})
